@media screen and (min-width: 320px) and (max-width: 767px) {
  .projects {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    font-family: 'OpenSans';
    padding: 40px 16px 0;
    background-color: #001337;
    
    &__title {
      font-style: normal;
      font-size: 15px;
      line-height: 22px;
      font-weight: 600;
      text-align: center;
      color: white;
      margin: 0 0 20px;
    }

    &__menu {
      display: flex;
      flex-direction: row;
      min-width: 288px;
      min-height: 42px;
      padding: 0;
      justify-content: space-between;
      align-items: flex-start;
      border-bottom: 2px solid #009FAC;
    }

    &__menu_all {
      font-size: 12px;
      line-height: 16px;
      font-weight: 600;
      text-align: center;
      color: white;
      margin: 16px 0 12px;
    }

    &__menu_pointer {
      margin: 0;
      margin: 22px 0 12px;
    }

    &__cards {
      margin: 0;
      padding: 20px 0 0;
      display: grid;
      gap: 8px;
      list-style-type: none;
      grid-template-columns: 1fr 1fr;
    }
  }

  .card {
    display: flex;
    flex-direction: column;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;

    .card__text {
      color: #fff;
      margin: 8px 0 0;
    }

    .card__text_large {
      color: #001337;
      margin: 0;
    }

    .card__button {
      width: 20px;
      height: 20px;
      background-image: url(../../images/Light.svg);
      background-size: cover;
    }
    .card__logo_gazprom {
      width: 60px;
      height: 26px;
      background-image: url(../../images/Gazprom.svg);
      background-size: cover;
    }

    .card__logo_sberbank {
      width: 106px;
      height: 26px;
      background-image: url(../../images/Sberbank.svg);
      background-size: cover;
    }
  }

  .card_large {
    grid-column-start: 1;
    grid-column-end: 3;
    padding: 16px;
    background-color: #E0F0FF;
    border-radius: 5px;
  }

  .card_small {
    background-color: #009FAC;
    border-radius: 5px;
    padding: 10px;
  }
}